@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP);
body {
  font-family: "Noto Sans JP", sans-serif;
  width: 80%;
  margin: 0 auto;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

div {
  background-image: url("https://drive.google.com/uc?   export=view&id=1XL9R63qOtkMXC-c2gS_r92BqBf1m0BIn");
  background: cover;
}

.buy {
  background: #999999;
  width: 300px;
  border-radius: 10px;
}

